/* General form wrapper */
.phone-form {
  background-color: #fff;
  max-width: 720px;
  margin: 40px auto;
  padding: 32px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-direction: column;
  gap: 24px;
}

/* Inputs & Textarea */
.form-input, .form-input-edit {
  width: 95%;
  padding: 12px 16px;
  border-radius: 8px;
  border: 1px solid #ddd;
  font-size: 16px;
  background-color: #fafafa;
  transition: border-color 0.2s ease;
}

.form-input:focus, .form-input-edit:focus {
  outline: none;
  border-color: #a338d9;
  background-color: #fff;
}

/* Form group for arrays (memory, colors, photos) */
.form-group, .form-group-edit {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label, .form-group-edit label {
  font-weight: 600;
  color: #333;
  margin-bottom: 4px;
}

/* Memory, Colors, Photos list */
.form-array, .form-array-edit {
  display: flex;
  gap: 12px;
  align-items: center;
}

/* Buttons */
.btn,
.btn-primary {
  background-color: #a338d9;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.btn:hover,
.btn-primary:hover {
  background-color: #6e15c8;
}
.cancel-btn{
  background-color: #a338d9;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  margin-left: 10px;
}

.btn-primary {
  align-self: flex-start;
  font-weight: bold;
}
.add-memory-btn{
  margin-top:10px;
}

.product-list {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
  padding: 20px;
}

.product-card {
  border: 1px solid #eee;
  padding: 15px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 0 10px rgba(0,0,0,0.05);
  text-align: center;
}

.product-image {
  width: 100%;
  height: auto;
  max-height: 50px;
  object-fit: cover;
  margin-top: 10px;
  border-radius: 8px;
}
.color-dots {
  display: flex;
  gap: 6px;
  margin-top: 5px;
}

.color-dot {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 1px solid #ccc;
}
.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: scroll;
  z-index: 1000;
}

.modal {
  background: white;
  padding: 24px;
  border-radius: 12px;
  width: 90%;
  height: 70%;
  max-width: 900px;
  overflow-y: scroll;
}
/* Responsive adjustments */
@media (max-width: 768px) {
  .phone-form {
    padding: 20px;
    margin: 20px;
  }

  .form-array {
    flex-direction: column;
    align-items: stretch;
  }

  .btn,
  .btn-primary {
    width: 100%;
  }
}

