.thumbnail-wrapper {
  position: relative;
  display: inline-block;
  margin: 8px;
}

.thumbnail-img {
  width: 100px;
  height: auto;
  border-radius: 8px;
}

.thumbnail-controls {
  display: flex;
  justify-content: center;
  gap: 4px;
  margin-top: 4px;
}

.remove-thumbnail-btn {
  background-color: #f44336;
  border: none;
  border-radius: 50%;
  box-shadow: 0 0 4px #0000004d;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  height: 35px;
  width: 22px;
  line-height: 22px;
  position: absolute;
  top: 4px;
  right: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2;
}




/* SHOW in shop */
.toggle-container {
  display: flex;
  align-items: center;
  gap: 20px;
  cursor: pointer;
  user-select: none;
  position: relative;
  margin: 15px 0;
}

.toggle-container input[type="checkbox"] {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
}

.toggle-switch {
  width: 46px;
  height: 24px;
  background-color: #ccc;
  border-radius: 999px;
  position: relative;
  transition: background-color 0.3s;
}

.toggle-switch::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 3px;
  width: 18px;
  height: 18px;
  background-color: #fff;
  border-radius: 50%;
  transition: transform 0.3s ease;
}

.toggle-container input:checked + .toggle-switch {
  background-color: #4caf50;
}

.toggle-container input:checked + .toggle-switch::before {
  transform: translateX(22px);
}

.toggle-label-text {
  font-size: 16px;
  color: #333;
}
.modal-actions {
  display: flex;
  gap: 12px; /* You can adjust spacing */
  margin-top: 20px;
}
