/* ───────────  DESIGN TOKENS  ─────────── */
:root{
  --clr-bg:           #f4f6f8;
  --clr-surface:      #ffffff;
  --clr-border:       #d0d7de;
  --clr-text:         #111827;
  --clr-muted:        #6b7280;
  --clr-primary:      #2563eb;
  --clr-primary-dark: #1e4fd9;
  --clr-success:      #2e7d32;
  --clr-success-dark: #256428;
  --clr-danger:       #c62828;
  --radius: 12px;
  --shadow-sm: 0 1px 3px rgba(0,0,0,.08);
  --shadow-lg: 0 8px 24px rgba(0,0,0,.12);
  --gap: .75rem;
  font-family: "Inter", system-ui, sans-serif;
}

/* ───────────  LAYOUT  ─────────── */
.broadcast-page{
  display:flex;
  justify-content:center;
  padding:2rem;
  background:var(--clr-bg);
  min-height:100vh;
}
.editor-wrapper{
  width:100%;
  max-width:1000px;
  background:var(--clr-surface);
  border:1px solid var(--clr-border);
  border-radius:var(--radius);
  box-shadow:var(--shadow-lg);
  display:flex;
  flex-direction:column;
  padding:1.25rem;
}

/* ───────────  TOOLBAR  ─────────── */
.toolbar{
  display:flex;
  flex-wrap:wrap;
  gap:var(--gap);
  margin-bottom:var(--gap);
}
.toolbar button{
  background:var(--clr-muted);
  color:#fff;
  border:none;
  padding:.45rem .8rem;
  border-radius:6px;
  cursor:pointer;
  transition:.15s;
  font-weight:600;
}
.toolbar button:hover{background:#4b5563}
.toolbar button.active,
.toolbar button:active{
  transform:translateY(1px);
  background:var(--clr-primary);
}
.upload-label{
  cursor:pointer;
}
.upload-label input{display:none}

/* ───────────  EMOJI PICKER  ─────────── */
.emoji-picker{
  margin-bottom:var(--gap);
  box-shadow:var(--shadow-sm);
  border-radius:var(--radius);
  overflow:hidden;
}

/* ───────────  EDITOR AREA  ─────────── */
.editor-content,
.modal-editor{
  flex:1 1 auto;
  min-height:280px;
  background:#fff;
  border:1px solid var(--clr-border);
  border-radius:6px;
  padding:1rem;
  overflow-y:auto;
  color:var(--clr-text);
}
.editor-content:focus-within,
.modal-editor:focus-within{
  outline:2px solid var(--clr-primary);
}

/* remove default gap between <p> */
.editor-content p,
.modal-editor p{margin:0 0 .5rem}

/* media inside editor */
.editor-content img,
.modal-editor img,
.editor-content video,
.modal-editor video{
  max-width:100%;
  height:auto;
  border-radius:6px;
  margin:.6rem 0;
  display:block;
}
.editor-content video,
.modal-editor video{max-height:360px}

/* ───────────  MAIN-PAGE BUTTONS  ─────────── */
.send-button{
  background:var(--clr-success);
  color:#fff;
  padding:.5rem 1.2rem;
  border:none;
  border-radius:6px;
  font-weight:600;
  cursor:pointer;
  transition:.15s;
  margin-right:.5rem;
  margin-top:5px;
}
.send-button:hover{background:var(--clr-success-dark)}
.send-button:disabled{opacity:.6; cursor:not-allowed}

/* ───────────  RADIO GROUP  ─────────── */
.button-type-selector{
  display:flex;
  gap:1.5rem;
  margin:.75rem 0 1.25rem;
  font-size:.875rem;
}
.button-type-selector input{
  accent-color:var(--clr-primary);
  margin-right:.35rem;
}

/* ───────────  DRAFT CARDS  ─────────── */
.draft-list{
  display:flex;
  gap:var(--gap);
  flex-wrap:wrap;
  padding:1rem;
}
.draft-card{
  flex:0 0 210px;
  background:var(--clr-surface);
  border:1px solid var(--clr-border);
  border-radius:var(--radius);
  padding:.7rem;
  cursor:pointer;
  transition:.15s box-shadow, .15s transform;
}
.draft-card:hover{
  box-shadow:var(--shadow-sm);
  transform:translateY(-2px);
}
.draft-card .tag{
  background:var(--clr-bg);
  padding:.15rem .5rem;
  border-radius:4px;
  font-size:.75rem;
  color:var(--clr-muted);
}

/* ───────────  MODAL  ─────────── */
.modal-backdrop{
  position:fixed; inset:0;
  background:rgba(0,0,0,.45);
  display:flex; justify-content:center; align-items:center;
  z-index:999;
  animation:fadeIn .25s ease;
}
@keyframes fadeIn{from{opacity:0}to{opacity:1}}
.modal{
  width:95%; max-width:700px;
  background:var(--clr-surface);
  border-radius:var(--radius);
  box-shadow:var(--shadow-lg);
  padding:1.25rem 1.5rem;
  max-height:92vh;
  overflow:auto;
}
.modal-actions{
  display:flex; flex-wrap:wrap; gap:var(--gap); margin-top:var(--gap);
}
.modal-actions button{
  padding:.45rem 1rem;
  border:none;
  border-radius:6px;
  font-weight:600;
  color:#fff;
  cursor:pointer;
  transition:.15s;
}
.modal-actions button:nth-child(1){background:var(--clr-primary);}
.modal-actions button:nth-child(1):hover{background:var(--clr-primary-dark);}
.modal-actions button:nth-child(2){background:var(--clr-success);}
.modal-actions button:nth-child(2):hover{background:var(--clr-success-dark);}
.modal-actions button:nth-child(3){background:var(--clr-danger);}
.modal-actions button:nth-child(3):hover{background:#b71c1c;}
.modal-actions button:nth-child(4){background:var(--clr-muted);}
.modal-actions button:nth-child(4):hover{background:#555;}

/* small screens */
@media (max-width:600px){
  .toolbar{gap:.5rem}
  .button-type-selector{flex-direction:column; gap:.4rem;}
  .draft-card{flex:1 1 calc(50% - .75rem)}
}
