.bot-manager {
  max-width: 760px;
  margin: 40px auto;
  padding: 32px;
  background-color: #fff;
  border-radius: 16px;
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.05);
  border: 1px solid #e5e5e5;
  font-family: 'Inter', sans-serif;
}

.bot-manager h2 {
  font-size: 30px;
  margin-bottom: 36px;
  text-align: center;
  font-weight: 700;
  color: #1e1e1e;
}

.bot-form {
  margin-bottom: 48px;
  padding-bottom: 32px;
  border-bottom: 1px solid #ddd;
}

.bot-form:last-child {
  border-bottom: none;
}

.bot-form h3 {
  font-size: 22px;
  margin-bottom: 20px;
  color: #333;
  display: flex;
  align-items: center;
  gap: 8px;
}

.bot-manager label {
  display: block;
  margin-bottom: 6px;
  font-weight: 600;
  color: #222;
  font-size: 14px;
}

.bot-manager input[type="text"],
.bot-manager input:not([type="file"]) {
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 16px;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #f9f9f9;
  font-size: 15px;
  color: #222;
  font-family: 'Inter', sans-serif;
  box-sizing: border-box;
  transition: border-color 0.3s ease, background-color 0.3s ease;
}

.bot-manager input[type="text"]:focus,
.bot-manager input:not([type="file"]):focus {
  border-color: #4CAF50;
  background-color: #fff;
  outline: none;
}

.bot-manager input::file-selector-button {
  background-color: #efefef;
  border: none;
  padding: 8px 14px;
  color: #222;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: background-color 0.2s ease;
}

.bot-manager input[type="file"]::file-selector-button:hover {
  background-color: #e0e0e0;
}

.submodel-group {
  background-color: #fafafa;
  border: 1px solid #e0e0e0;
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 24px;
}

.bot-manager button {
  background-color: #4CAF50;
  color: #fff;
  font-weight: 600;
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 16px;
  display: inline-block;
  margin-top: 8px;
  margin-right: 10px;
}

.bot-manager button:hover {
  background-color: #43a047;
}

@media (max-width: 600px) {
  .bot-manager {
    padding: 24px;
  }

  .bot-manager h2 {
    font-size: 24px;
  }

  .bot-form h3 {
    font-size: 20px;
  }

  .bot-manager button {
    width: 100%;
    margin-right: 0;
    margin-top: 12px;
  }
}


.entry-list {
  margin-top: 24px;
  padding: 16px;
  border-top: 1px solid #ccc;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.entry-item {
  padding: 12px 16px;
  background: #f4f4f4;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s;
}

.entry-item:hover {
  background: #eaeaea;
}

.custom-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.65);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  padding: 20px;
  box-sizing: border-box;
}

.custom-modal {
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  width: 100%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
  animation: fadeIn 0.25s ease-out;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal-submodel {
  margin-bottom: 24px;
  border-top: 1px solid #ddd;
  padding-top: 16px;
}

.modal-submodel h4 {
  margin-bottom: 4px;
  font-size: 1.1rem;
}

.media-preview {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  margin-top: 10px;
}

.media-preview img,
.media-preview video {
  max-width: 150px;
  max-height: 150px;
  border-radius: 8px;
  object-fit: cover;
  background: #000;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.custom-modal button {
  margin-right: 12px;
  padding: 10px 18px;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.2s;
}

.custom-modal button:first-of-type {
  background: #ff4d4f;
  color: white;
}

.custom-modal button:first-of-type:hover {
  background: #d9363e;
}

.custom-modal button:last-of-type {
  background: #f0f0f0;
}

.custom-modal button:last-of-type:hover {
  background: #e0e0e0;
}
